@import 'base';

.content {
    font-family: $font-stack;
    font-size: 3vmin;
    background-color: white;
    color: $background-color;
    width: 100%;
    margin: 0;
    padding: 5vh 5vw;
    z-index: 2;
}

.content h1 {
    font-weight: bolder;
    text-align: center;
    margin-bottom: 2vmin;
    font-size: 5vmin;
}

.section {
    margin: 0;
    margin-top: 2vmin;
    margin-bottom: 12vmin;
    padding: 0;
}

.buttonRow {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin: 0;
    padding: 0;
}

.logoButton {
    width: 20vmin;
    height: 25vmin;
    margin: 0;
    padding: 0;
}

.logoButton div {
    width: 100%;
    height: 80%;
    margin: 0;
    padding: 0;
}

.logoButton div a img {
    width: 100%;
    height: 100%;
}

.logoButton div a img:nth-child(1) {
    filter: brightness(0) saturate(100%) invert(26%) sepia(18%) saturate(321%) hue-rotate(187deg) brightness(90%) contrast(86%);
    position: relative;
    top: 2.5%;
    left: 0;
}

.logoButton div a img:nth-child(2) {
    filter: brightness(0) saturate(100%) invert(48%) sepia(33%) saturate(933%) hue-rotate(231deg) brightness(86%) contrast(85%);
    position: relative;
    top: -100%;
    left: 0;
    transition: top 0.2s, filter 0.2s;
}
.logoButton div a img:nth-child(2):hover {
    filter: brightness(0) saturate(100%) invert(63%) sepia(100%) saturate(2195%) hue-rotate(222deg) brightness(88%) contrast(83%) !important;
    top: -101.5%;
}

.logoButton p {
    width: 100%;
    height: 15%;
    margin: 0;
    margin-top: 5%;
    padding: 0;
    text-align: center;
    font-size: 3vmin;
}